import React from "react"
import { graphql } from "gatsby"

export default ({ data }) => {
  return (
    <div style={{ color: "white" }}>
      <img src={'../../' + data.illuminatorDataYaml.image}/>
      {data.illuminatorYaml.text}
    </div>
  )
}

export const query = graphql`
    query($name: String!) {
        illuminatorDataYaml(name: {eq: $name}) {
            image
            text
        }
    }
`
